
import {
  reactive,
  onMounted,
  toRefs,
  h,
  getCurrentInstance,
  computed,
  ref,
  toRaw,
  defineComponent,
} from "vue";
import { Operation } from "@element-plus/icons";
export default defineComponent({
  components: {
    Operation,
  },
  setup() {
    const state = reactive({
      rgbaVal: ref(""),
      hexadecimalVal: ref(""),
    });
    const changePos = () => {};
    //转换为RGBA
    const toRgba = () => {
      state.rgbaVal = `rgba(${parseInt(
        "0x" + state.hexadecimalVal.slice(1, 3)
      )},${parseInt("0x" + state.hexadecimalVal.slice(3, 5))},${parseInt(
        "0x" + state.hexadecimalVal.slice(5, 7)
      )},1)`;
    };
    //转换为16进制
    const toHexadecimal = () => {
      // RGB颜色值的正则
      let reg = /^(rgba|RGBA)/;
      let color = state.rgbaVal;
      if (reg.test(color)) {
        let strHex = "#";
        // 把RGB的3个数值变成数组
        let colorArr:any = color.replace(/(?:\(|\)|rgba|RGBA)*/g, "").split(",");
        // 转成16进制
        for (let i = 0; i < colorArr.length-1; i++) {
          let hex = Number(colorArr[i]).toString(16);
          if (hex === "0") {
            hex += hex;
          }
          strHex += hex;
        }
        strHex = `${strHex},${colorArr[3]*100}%`
        state.hexadecimalVal = strHex;
      }
    };
    return {
      ...toRefs(state),
      toRgba,
      toHexadecimal,
      changePos,
    };
  },
});
